.setfooter {
    height: 230px;
    margin-top: auto;
    margin-bottom: auto;
    background-color: #2e3031;
}
.fontfooter1 {
    font-size: larger;
    text-align: center;
}
.fontfooter2 {
    text-align: right;
    color: #ffffff;
}
.setcol-left {
    margin-top: 40%;
}
.setcol-right {
    margin-top: 40px;
}
.footer {
    width: 100%;
    margin: 0;
    .navbar-footer__menu-box {
        padding-top: 0;
        margin-top: 15px;
        margin-bottom: 15px;
        ul {
            list-style-type: none;
            margin: 0;
            padding: 0;
            overflow: hidden;
        }
        li {
            float: right;
            border-right: 1px solid white;
            &:first-child {
                border-right: 0 solid #666;
                a {
                    padding-right: 0;
                }
            }
            a {
                display: block;
                color: white;
                text-align: center;
                padding: 14px 16px;
                text-decoration: none;
                padding-top: 0;
                padding-bottom: 0;
                &:hover {
                    color: #23b14c;
                }
            }
        }
    }
}
